	module.exports = {"data":{"post":{"html":"<p>PapaScott has moved. Hopefully you didn't even notice. But it will take until Sunday morning for everyone's DNS to catch up. If you're reading this, you're at the new address. Welcome aboard.</p>\n<p>Here at the new place we have the new version of <a href=\"http://www.cafelog.com/\">b2</a> installed, with various improvements that are mostly invisible to the naked eye. And now that I can put .htaccess settings wherever I want, I'm going to do something about those unreadable 'index.php?blah=foo&amp;bluh=bar' URLs. I hope.</p>","fields":{"title":"New Home","date":"09 Nov 2002","url":"https://papascott.de/2002-11-09-new-home/"}}},"pathContext":{"slug":"/2002-11-09-new-home/","prev":{"fields":{"title":"Visionary of German journalism","date":"08 Nov 2002","slug":"/2002-11-08-visionary-of-german-journalism/"}},"next":{"fields":{"title":"RSS Uproar","date":"10 Nov 2002","slug":"/2002-11-10-rss-uproar/"}}}}

